


import Vue from 'vue'

import VueRouter from 'vue-router'
import App from './App'
import Home from '@/components/home'
import Start from '@/components/start'

import About from '@/components/about'
import Daily from '@/components/daily'
import Disclaimer from '@/components/disclaimer'
import Spread from '@/components/spread'
import SpreadResult from '@/components/spreadresult'

import Storage from 'vue-ls'

import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

require('vue2-animate/dist/vue2-animate.min.css')

import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

import langTw from '@/assets/lang/tw'
import langCn from '@/assets/lang/cn'
import langEn from '@/assets/lang/en'

Vue.component('XButton', XButton)
Vue.component('divider', Divider)
Vue.component('flexbox', Flexbox)
Vue.component('flexbox-item', FlexboxItem)

Vue.use(Storage)
Vue.use(VueRouter)

const i18n = new VueI18n({
locale: 'en',
fallbackLocale: 'en',
messages:{
'zh-TW':langTw,
'zh-CN':langCn,
'en':langEn
}
})



const routes = [{
path: '/',
component: Home
},{
path: '/start',
component:Start,
},{
path: '/about',
component:About,
},{
path: '/daily',
component:Daily,
},{
path: '/disclaimer',
component:Disclaimer,
},{
path: '/spread',
component:Spread,
},{
name:'result',
path: '/result/:num',
component:SpreadResult
}
]
const router = new VueRouter({
routes
})




Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router, i18n,
render: h => h(App)
}).$mount('#app')



