


import html2canvas from 'html2canvas'

let daily = require('./draw.js').daily

export default {
data () {
return {
time:{
month:Number,
date:Number,
hours:Number,
},
restHour:24-(new Date).getHours(),
msg:"Choose one",
startTouchTime:0,
draw:false,
path:'',
mean:'',
card:'',
flag:'',
cardName:'',
flagName:'',
style:'',
google:'https://google.com/search?q='
}
},
created(){
let rc = this.$ls.get('daily','nothing')
if(rc=='nothing') return;

this.render(rc);
},
methods:{
dailyshot(){
let result = document.querySelector('.result');
html2canvas(result).then(canvas=>{
let dataUrl = canvas.toDataURL();
var link = document.createElement('a');
let now = new Date;
link.download =`daily-${now.getMonth()+1}-${now.getDate()}.jpg`;
link.href = dataUrl;
link.click();
})
},
render(d){
if(d.reversed) this.style='transform: scaleY(-1);'
this.path = d.path;
this.mean = d.mean;
if(this.$root.$i18n.locale ==='zh-TW' || this.$root.$i18n.locale==='zh-CN'){
this.cardName = d.card.tw;
this.flagName =d.flag.tw;
}else{
this.cardName = d.card.en;
this.flagName = d.flag.en;
}

this.time = d.time;
this.msg="Go Back"
this.draw = true;

},
tStart(e){
this.startTouchTime = e.timeStamp;
this.size+=50;
},
tEnd(e){
let d = daily();
let now = new Date;
d.time = {
'month':now.getMonth()+1,//會差一個月
'date':now.getDate(),
'hours':now.getHours()
};
let h = (24 - now.getHours())
let m = (60- now.getMinutes())

let restTime = ((h-1)*60 + m)*60 *1000; 

this.render(d);
this.$ls.set(
'daily',{
'time':this.time,
'card':d.card,
'flag':d.flag,
'path':d.path,
'reversed':d.reversed,
'mean':d.mean
},restTime);//a day expire
}
}
}



